/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role='list'],
ol[role='list'] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  -webkit-text-decoration-skip: ink;
          text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
   scroll-behavior: auto;
  }
  
  *,
  *::before,
  *::after {
    -webkit-animation-duration: 0.01ms !important;
            animation-duration: 0.01ms !important;
    -webkit-animation-iteration-count: 1 !important;
            animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
    --background-color: rgb(32, 32, 32);
    --text-color: #fff;
    --link-border-color: rgb(134, 134, 134);
}

* {
    box-sizing: border-box;
  }



body {
    font-family: 'Flamenco', cursive;
    background-color: rgb(32, 32, 32);
    background-color: var(--background-color);
    color: #fff;
    color: var(--text-color);
    font-weight: 300;
    display: block;
    position: relative;
}

/* default styling for tags */
.default-tag-structure {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    flex-wrap: wrap;
    border-bottom: 1px solid #fff;
    border-bottom: 1px solid var(--text-color);
    margin-bottom: 50px;
    justify-content: space-around;
    overflow: hidden;
}

/* header styling */
.link-list {
    display: flex;
    padding-bottom: 30px;
}

.nav-item {
    border-radius: 3px;
    padding: 5px 10px 2px 10px;
    border-top: 2px solid rgb(134, 134, 134);
    border-top: 2px solid var(--link-border-color);
    border-left: 1px solid rgb(134, 134, 134);
    border-left: 1px solid var(--link-border-color);
    border-bottom: 1px solid rgb(134, 134, 134);
    border-bottom: 1px solid var(--link-border-color);
    border-right: 1px solid rgb(134, 134, 134);
    border-right: 1px solid var(--link-border-color);
    margin: 15px;
    text-align: center;
    box-shadow: 1px 1px 5px #000;
    list-style-type: none;
}

.nav-item a {
    color: rgb(134, 134, 134);
    color: var(--link-border-color);
    padding: 0px;
    font-size: 1.6rem;
    font-weight: 400;
    text-shadow: 2px 2px 2px rgb(0, 0, 0);
}

/* header image class */
.headShot {
    height: 300px;
    width: auto;
}

/* End header styling */

/* Main styling  */

main.default-tag-structure {
    margin-bottom: 0px;
}

section.default-tag-structure {
    border-bottom: none;
}

p.article-text {
    margin: 50px 50px 0px 50px;
    font-size: 1.3rem;
}

footer.default-tag-structure {
    margin: 50px 0px 50px 0px;
    border-bottom: none;
}

.project-images-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
    width: 610px;

}

.project-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.project-info {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    width: 600px;
}

.techs {
    border-bottom: 1px solid #fff;
    border-bottom: 1px solid var(--text-color);
}

.title-section {
    display: flex;
    align-items:flex-end;
}

.image-container {
    display: flex;
}

.project-images {
    height: auto;
    width: 550px;
    box-shadow: 0px 30px 30px #000;
    margin: 50px 30px 0px 30px;
    display: flex;
    justify-content: center;
}

.project-title {
    font-size: 1.8rem;
    border-bottom: 1px solid #fff;
    border-bottom: 1px solid var(--text-color);
    padding: 0px 10px 0px 10px;
    color: #fff;
    color: var(--text-color);
}

li.default-tag-structure {
    border-bottom: none;
    margin-bottom: 0px;
}
.contact-icons-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    width: 600px;
    padding: 3px 0px 50px 0px;
}

.icons {
    width: 50px;
    height: auto;
}

#contact-me.default-tag-structure {
    margin-bottom: 0px;
}

/* end main styling */

/* text elements */

h1 {
    font-size: 2.5rem;
    padding: 0px 4px 0px 4px;
    border-bottom: 1px solid #fff;
    border-bottom: 1px solid var(--text-color);
    text-align: center;
}

h3 {
    font-size: 1.3rem;
    color: #fff;
    color: var(--text-color);
    font-style: italic;
    margin-left: 10px;
    border-bottom: 1px solid #fff;
    border-bottom: 1px solid var(--text-color);
}

p {
    text-align: center;
    margin: 10px 50px 0px 50px;
    font-size: 1.6rem;
    line-height: 2.6rem;
    /* border-bottom: 1px solid var(--text-color); */
}

a {
    text-decoration: none;
}

footer.main-section {
    margin-bottom: 0px;
    border: none;
}

/* end text styling */

/* hover behavior */

/* nav bar */
.link-list li:hover {
    box-shadow: 0px 0px 3px rgb(199, 199, 199);
}

/* images */
.project-images:hover {
    border: 1px solid #fff;
    border: 1px solid var(--text-color);
    box-shadow: 10px 35px 35px #000;
}

/* contact icons */
.icons:hover {
    box-shadow: 0px 0px 3px rgb(199, 199, 199);
}
/* end hover behavior */

#dl-btn {
    margin: 10px;
    padding: 6px;
}

.resume-section {
    /* display: flex; */
    /* flex-wrap: wrap; */
}

.resume-section .resume-list {
    justify-content: center;
}

.resume-list {
    font-size: 1.5rem;
}

.resume-list h3 {
    font-size: 2rem;
    margin-top: 40px;
    margin-left: 0px;
}

.resume-list h3 #first {
    margin-top: 0%;    
}

.resume-list li {
    text-align: center;
}

.form {
    color: #fff;
    color: var(--text-color);
    background-color: rgb(32, 32, 32);
    background-color: var(--background-color);
    font-size: 1.5rem;
}

#submit-btn {
    margin: 10px;
}

/* Media Queries */
@media screen and (max-width: 530px) {
    .link-list {
        flex-direction: column;
    }
}

@media screen and (max-width: 1300px) {
    .project-images-container {
        width: 600px;
    }
}

@media screen and (max-width: 610px) {
    .project-images {
        width: 270px;
        margin: 50px 20px 20px 20px;
    }

    .project-images-container, .project-info {
        flex-direction: column;
        width: 276px;
    }

    .project-images-container .project-images {
        width: 270px;
    }

    .contact-icons-list {
        width: 230px;
    }
    .icons {
        width: 40px;
        margin: 10px;
    }

    form input, textarea{
        display: block;
        margin: 20px;
        width: auto;
    }
}

@media screen and (max-width: 310px) {
    .project-images, .project-info{
        width: 270px;
        margin: 50px 5px 20px 5px;
    }
}
